"use client";

import { useState } from "react";
import {
  AiFillHome,
  AiOutlineVideoCamera,
  AiOutlineSetting,
  AiOutlineFolder,
} from "react-icons/ai";
import { FaChevronDown, FaChevronUp } from "react-icons/fa";
import Link from "next/link";

const MENUS = {
  HOME: "home",
  CAMERA: "camera",
  ARCHIVE: "archive",
  SETTINGS: "settings",
};

const Sidebar = () => {
  const [isCameraOpen, setIsCameraOpen] = useState(false);
  const [activeMenu, setActiveMenu] = useState<string>(MENUS.HOME);
  const [activeCamera, setActiveCamera] = useState<string | null>(null);

  const toggleCamera = () => setIsCameraOpen((prev) => !prev);

  return (
    <div className="w-64 h-full bg-white text-[#5D6679] flex flex-col shadow-lg">
      {/* Logo */}
      <div className="flex items-center justify-center p-6">
        <img
          src="/logo.png"
          alt="Logo"
          className="h-12 w-auto object-contain"
        />
      </div>

      {/* Menu List */}
      <ul className="flex-1 mt-4 space-y-2 px-4">
        {/* Home */}
        <Link
          href="/authenticated/dashboard"
          className={`flex items-center gap-3 p-3 rounded-lg cursor-pointer transition-all ${
            activeMenu === MENUS.HOME
              ? "bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-md"
              : "hover:bg-gray-100 text-[#5D6679]"
          }`}
          onClick={() => {
            setActiveMenu(MENUS.HOME);
            setActiveCamera(null);
          }}
        >
          <AiFillHome size={20} />
          <span className="text-sm font-medium">Home</span>
        </Link>

        {/* Camera */}
        <div
          className={`flex items-center justify-between p-3 rounded-lg cursor-pointer transition-all ${
            activeMenu === MENUS.CAMERA
              ? "bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-md"
              : "hover:bg-gray-100 text-[#5D6679]"
          }`}
          onClick={() => {
            toggleCamera();
            setActiveMenu(MENUS.CAMERA);
          }}
        >
          <div className="flex items-center gap-3">
            <AiOutlineVideoCamera size={20} />
            <span className="text-sm font-medium">Camera</span>
          </div>
          {isCameraOpen ? <FaChevronUp size={16} /> : <FaChevronDown size={16} />}
        </div>

        {/* Camera Dropdown */}
        {isCameraOpen && (
          <ul className="ml-5 mt-1 space-y-1">
            {["cam1", "cam2", "cam3", "cam4"].map((cam) => (
              <Link
                href={`/authenticated/camera/${cam}`}
                key={cam}
                className={`flex items-center gap-2 p-2 rounded-md cursor-pointer transition-all ${
                  activeCamera === cam
                    ? "bg-blue-100 text-blue-600 font-medium"
                    : "hover:bg-gray-100 text-[#5D6679]"
                }`}
                onClick={() => setActiveCamera(cam)}
              >
                <div
                  className={`h-2 w-2 rounded-full ${
                    activeCamera === cam ? "bg-blue-600" : "bg-gray-400"
                  }`}
                ></div>
                <span className="text-xs">Camera {cam.slice(-1)}</span>
              </Link>
            ))}
          </ul>
        )}

        {/* Archive */}
        <Link
          href="/authenticated/archive"
          className={`flex items-center gap-3 p-3 rounded-lg cursor-pointer transition-all ${
            activeMenu === MENUS.ARCHIVE
              ? "bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-md"
              : "hover:bg-gray-100 text-[#5D6679]"
          }`}
          onClick={() => {
            setActiveMenu(MENUS.ARCHIVE);
            setActiveCamera(null);
          }}
        >
          <AiOutlineFolder size={20} />
          <span className="text-sm font-medium">Archive</span>
        </Link>

        {/* Settings */}
        <Link
          href="/authenticated/settings"
          className={`flex items-center gap-3 p-3 rounded-lg cursor-pointer transition-all ${
            activeMenu === MENUS.SETTINGS
              ? "bg-gradient-to-r from-blue-500 to-blue-600 text-white shadow-md"
              : "hover:bg-gray-100 text-[#5D6679]"
          }`}
          onClick={() => {
            setActiveMenu(MENUS.SETTINGS);
            setActiveCamera(null);
          }}
        >
          <AiOutlineSetting size={20} />
          <span className="text-sm font-medium">Settings</span>
        </Link>
      </ul>

      {/* Footer */}
      <div className="p-3 text-xs text-center text-[#5D6679] border-t border-gray-200">
        Â© 2025 CamUI
      </div>
    </div>
  );
};

export default Sidebar;
